<template>
  <div class="creditCard-wrap" v-title="'信用卡中心'">
    <!-- 筛选filterTap -->
    <article class="filterTapWap">
      
      <div class="slide-nav">
        <div v-for="(item,index) in list" :key="index" class="iconfont icon-triangle-up" :class="active==item.label?'active':''" size="small" @click.prevent="active = item.label;">{{item.label}}</div>
      </div>
      <div class="page-tab-container">
        <mt-tab-container class="page-tabbar-tab-container" v-model="active" >
          <mt-tab-container-item v-for="(item,index) in list" :key="index" :id="item.label">
            
            <div class="item" v-for="(sub,index) in item.content" :key="index">
              <div class="sort" v-if="sub.sort!=''">{{sub.sort}}</div>
              <div class="subcontent">
                <div class="subcontent-li" v-for="(subchild,index) in sub.subcontent" :key="index">
                  {{subchild}}

                </div>
              </div>
              

            </div>
          </mt-tab-container-item>
        </mt-tab-container>
      </div>
    </article>
    <!-- 信用卡列表 -->
    <article class="cardWrap">

      <scroller v-if="cardListShow" :on-refresh="reFresh" :on-infinite="loadMore">
        <card-list :card="item" v-for="(item,index) in cardList" :key="index"></card-list>
      </scroller>
      <div v-if="isEmpty" class="empty"><img src="~@/assets/images/empty_wap.png" alt=""></div>
    </article>
  </div>
</template>
<script>
// import { swiper, swiperSlide } from 'vue-awesome-swiper'
import cardTitle from '@/components/common/IndexCommon/cardTitle'
import cardList from '@/components/common/IndexCommon/cardList'
import utils from '@/modules/utils'


let vm = null
export default {
  name: 'home',
  components: {
    // swiper,
    // swiperSlide,
    cardTitle,
    cardList,

  },
  data() {
    return {
      isShow:false,
      active: '',
      list: [
        {
          label: '全部银行',
          content: [{
            sort: '',
            subcontent: ['全部银行', '广发银行', '建设银行']
          }]
        },
        {
          label: '等级',
          content: [{
            sort: '',
            subcontent: ['全部等级', '普卡', '白金卡']
          }]
        },
        {
          label: '用途',
          content: [{
            sort: '',
            subcontent: ['全部用途', '大额取现', '商旅人士']
          }]
        },
        {
          label: '更多',
          content: [{
              sort: '年费',
              subcontent: ['永久免年费', '交易累计免年费', '不免年费']
            },
            {
              sort: '卡组织（多选）',
              subcontent: ['银联','VISA','运通','JCB','万事达']
            }
          ]
        }

      ],
      formList:[],
      cardListShow: true,
      page: 1,
      totalPage: 1,
      isEmpty: false,
      cardList: []
    }
  },
  computed: {
    banner() {
      return this.bannerList
    },
    bank() {
      return this.bankList
    }
  },
  created() {
    vm = this
    this.getCardList()
    this.test()

  },
  watch: {

  },
  methods: {
    //下拉刷新
    reFresh(done) {
      this.page = 1;
      this.cardList = [];
      this.getCardList(done);
    },
    //滚动列表加载更多
    loadMore(done) {
      if (this.page < this.totalPage) {
        this.page++;
        this.getCardList(done);
      }else{
        done&&done(false)
      }

    },
    // 办卡进度跳转
    toCardProgress() {
      this.$router.push({ name: 'creditCardProgressPage' })

    },
    handleClickSlide(index) {
      let item = this.bannerList[index]
      this.addClick(item)
    },
    addClick(item) {

      this.clickNum(item.id)
      window.open(item.url, '_self')
    },

    // getData-start
    async test() {
      let json = {
        page: 2,
        limit: 4
      }
      let res = await this.$http.postData('/app/find/newslist/', json)
      console.log(res)
    },

    async getCardList(done) {
      let json = {
        page: this.page,
        limit: 15
      }
      let res = await this.$http.postData('/app/creditCard/getList', json)
      console.log(res)
      if (res.page.list.length > 0) {

        this.totalPage = res.page.totalPage;
        let d = res.page.list;
        ////对象数组的相加 A+B
        for (var i = 0; i < d.length; i++) {
          this.cardList.push(d[i])

        }
        ////对象数组的合并 A u B !=A+B;
        // this.cardList = Object.assign([], this.cardList, res.page.list)

        // console.log(this.cardList)
        this.cardListShow = true
        done&&done(true)
        // done && done(true)
      } else {
        this.cardListShow = false
        this.isEmpty = true
      }
    },

    // getData-end

    HandleDialog() {
      this.isShowDialog = !this.isShowDialog
    },
    HandleResult() {
      this.isShowResult = !this.isShowResult
    }
  }
}

</script>
<style lang="postcss" scoped>
*{box-sizing:border-box;}
.creditCard-wrap {
  & .filterTapWap{
    position: fixed;
    top: 0;
    z-index: 1;
    background: #fff;
    width: 100%;
    & .slide-nav{
      background: #FBFBFB;
      border-bottom: .5px solid #E6E6E6;
      & .icon-triangle-up{
        font-size:14px;
        border-bottom: 1px solid #fff;
      }
      & .icon-triangle-up.active{
        color:#FF7909;
        
      }
      & .icon-triangle-up.active:after {
        
        transform: rotate(180deg);
        
      }
      & .icon-triangle-up:after {
        display:inline-block;
        content: "\E60A";
      }
      & .icon-triangle-up:before {
          content: "";
      }
    }

    & .page-tab-container{
      & .item{
        display:flex;
        flex-direction: column;
        margin:10px 0px;

        & .sort{
 
          font-size: 12px;
          color: #A4A4A4;
          padding:0 16px;
        }
        & .subcontent-li{
          font-size:16px;
          padding:16px;
          border-bottom:.5px solid #eee;
        }
        & .subcontent-li:nth-of-type(1){
          font-size:18px;
          color:#FF7909;


        }
      }
      & .mint-tab-container-item:nth-last-of-type(1) .item .subcontent{
        margin:0 0 0 16px;
        & .subcontent-li{
          float:left;
          width:110px;
          height:28px;
          font-size: 12px;
          color: #1C2042;
          background: #F3F3F3;
          border-radius: .3em;
          padding: 0;
          margin:8px;
          margin-left:0px;
          display:flex;
          justify-content:center;
          align-items:center;
        }
        & .subcontent-li:nth-of-type(3){
          margin-right:0;
        }
      }
    }
  }
  & .cardWrap {
    overflow: hidden;
  }
  & .slide-nav {
    padding: 0 16px;
    display: flex;
    position: -webkit-sticky;
    position: sticky;
    &>div {
      width: 50%;
      color: #1c2042;
      font-size: 14px;
      line-height: 40px;
      text-align: center;
    }
    &>div.active {
      
      border-bottom: 1px solid #FF7909;
    }
  }
}

</style>



